.loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: hidden;
    pointer-events: none;
    & > div {
        font-size: 18px;
        font-family: 'fmain', sans-serif;
        color: #fff;
        text-transform: uppercase;
        font-weight: normal;
        letter-spacing: 10px;
        mix-blend-mode: difference;
        white-space: nowrap;
        opacity: 0;
    }
    &__hello {
        animation: show 7s linear;
        animation-delay: 1s;
    }
}

@keyframes line {
    0% {
        opacity: 0;
    }
    70% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        opacity: 1;
    }
}

@keyframes show {
    0% {
        opacity: 0;
    }
    30% {
        opacity: 0.6;
    }
    50% {
        opacity: 1;
    }

    100% {
        opacity: 1;
    }
}

.logo {
    display: block;
    margin-bottom: 10px;
    animation: fill 0.5s ease forwards 3s;
}

.logo path:nth-child(2) {
    stroke-dasharray: 311.8308410644531px;
    stroke-dashoffset: 311.8308410644531px;
    animation: line 2s ease-in-out forwards 0.3s, fill 0.5s linear forwards 0.4s;
}
.logo path:nth-child(3) {
    stroke-dasharray: 245.9285125732422px;
    stroke-dashoffset: 245.9285125732422px;

    animation: line 2s ease-in-out forwards 0.6s, fill 0.5s linear forwards 0.8s;
}
.logo path:nth-child(4) {
    stroke-dasharray: 168.5198211669922px;
    stroke-dashoffset: 168.5198211669922px;

    animation: line 2s ease-in-out forwards 0.9s, fill 0.5s linear forwards 1.2s;
}

.logo path:nth-child(5) {
    stroke-dasharray: 197.3889923095703px;
    stroke-dashoffset: 197.3889923095703px;

    animation: line 2s ease-in-out forwards 1.2s, fill 0.5s linear forwards 1.4s;
}
.logo path:nth-child(6) {
    stroke-dasharray: 165.09848022460938px;
    stroke-dashoffset: 165.09848022460938px;

    animation: line 2s ease-in-out forwards 1.5s, fill 0.5s linear forwards 1.8s;
}
.logo path:nth-child(7) {
    stroke-dasharray: 267.82476806640625px;
    stroke-dashoffset: 267.82476806640625px;

    animation: line 2s ease-in-out forwards 1.8s, fill 0.5s linear forwards 2.1s;
}
.logo path:last-child {
    stroke-dasharray: 168.51930236816406px;
    stroke-dashoffset: 168.51930236816406px;

    animation: line 2s ease-in-out forwards 2.1s, fill 0.5s linear forwards 2.3s;
}

@keyframes line {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes fill {
    from {
        fill: transparent;
    }
    to {
        fill: black;
    }
}
