* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html,
body {
    /* width: 100%;
  height: 100%; */
    background-color: #000;
    scroll-behavior: smooth;
    text-rendering: optimizeLegibility;
    

}

a {
    text-decoration: none;
}


@font-face {
    font-family: 'fmain';
    src: url('../src/assets/fonts/LEMONMILKProFTR-Regular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
    font-display: fallback;
}
@font-face {
    font-family: 'll';
    src: url('../src/assets/fonts/LEMONMILKProFTR-UltraLight.woff2') format('woff2');
    font-weight: 200;
    font-style: normal;
    font-display: fallback;
}

@font-face {
    font-family: 'lb';
    src: url('../src/assets/fonts/LEMONMILKProFTR-Bold.woff2') format('woff2');
    font-weight: bold;
    font-style: normal;
    font-display: fallback;
}

@font-face {
    font-family: 's';
    src: url('../src/assets/fonts/TheScientistRegular.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
    font-display: fallback;
}

@font-face {
    font-family: 'g';
    src: url('../src/assets/fonts/Glysa-Medium.woff2') format('woff2');
    font-weight: 500;
    font-style: normal;
    font-display: fallback;
}
